const escapeHtml = str => {
  return str
    .replace(/&amp;/g, "&")
    .replace(/&lt;/g, "<")
    .replace(/&gt;/g, ">")
    .replace(/&quot;/g, "'")
    .replace(/&#039;/g, "'")
    .replace(/&eacute;/g, "")
    .replace(/&divide;/g, "")
    .replace(/&pi;/g, "")
    .replace(/&Pi;/g, "")
    .replace(/&Uuml;/g, "")
    .replace(/&Delta;/g, "")
    .replace(/&micro;/g, "")
    .replace(/&oacute;/g, "")
    .replace(/&uacute;/g, "")
    .replace(/&aacute;/g, "")
    .replace(/&ntilde;/g, "")
    .replace(/&Eacute;/g, "")
    .replace(/&ldquo;/g, "")
    .replace(/&ecirc;/g, "")
    .replace(/&rdquo;/g, "")
    .replace(/&hellip;/g, "...")
    .replace(/&shy;/g, "- ")
    .replace(/&deg;/g, "")
    .replace(/&iacute;/g, "")
    .replace(/&rsquo;/g, "")
    .replace(/&uuml;/g, "");
};

export default escapeHtml;
